import React, { Component, createRef } from "react";
import { Modal, Button, message } from "antd";
import { PostForm } from "./PostForm";
import axios from "axios";

import { BASE_URL, TOKEN_KEY } from "../constants";

class CreatePostButton extends Component {
 state = {
   visible: false,
   confirmLoading: false
 };

 showModal = () => {
   this.setState({
     visible: true
   });
 };

 handleOk = () => {
   this.setState({
     confirmLoading: true
   });

   // get form data
   this.postForm
     //value
     .validateFields()
     .then((form) => {
        //form
       const { description, uploadPost } = form;
       const { type, originFileObj } = uploadPost[0];
       const postType = type.match(/^(image|video)/g)[0];
       //
       if (postType) {
        //JSformDataApi
         let formData = new FormData();
         formData.append("message", description);
         formData.append("media_file", originFileObj);

         const opt = {
           method: "POST",
           url: `${BASE_URL}/upload`,
           headers: {
             Authorization: `Bearer ${localStorage.getItem(TOKEN_KEY)}`
           },
           data: formData
         };

         axios(opt)
           .then((res) => {
             if (res.status === 200) {
               message.success("Successfully uploaded!");
               //form
               this.postForm.resetFields();
               //
               this.handleCancel();
               //imageimage
               this.props.onShowPost(postType);
               //loading
               this.setState({ confirmLoading: false });
             }
           })
           .catch((err) => {
             console.log("Upload failed: ", err.message);
             message.error("Failed to upload");
             this.setState({ confirmLoading: false });
           });
       }
     })
     .catch((err) => {
       console.log("err ir validate form -> ", err);
     });
 };

 handleCancel = () => {
   console.log("Clicked cancel button");
   this.setState({
     visible: false
   });
 };

 render() {
   const { visible, confirmLoading } = this.state;
   return (
     <div>
       <Button type="primary" onClick={this.showModal}>
         New Post
       </Button>
       <Modal
         title="Upload a New Post"
         visible={visible}
         onOk={this.handleOk}
         okText="Upload"
         confirmLoading={confirmLoading}
         onCancel={this.handleCancel}
       >
        <PostForm ref={(refInstance) => (this.postForm = refInstance)} />
       </Modal>
     </div>
   );
 }
}
export default CreatePostButton;